.paragraph {
  margin: -5px 10px 0 10px;
  padding: 0 0 10px;
  display: inline-block;
  width: 90%;
}

.paragraph label {
  display: block;
  font-size: 1.2em;
  margin-bottom: 5px;
  font-weight: bold;
}

.paragraph textarea {
  display: block;
  font-size: 1.7em;
  width: 90%;
  max-width: 90%;
  max-height: 30%;

}