.add {
  display: block;
  width: 35%;
  padding: 65px 10px 10px;
}

.add button {
  display: block;
  width: 50%;
  font-size: 1.2em;
  padding: 20px;
  border-radius: 12px;
  margin-top: -50px;
  cursor: pointer;
}

@media (max-width: 1448px) {
  .add {
    float: none;
    width: 100%;
    max-width: 500px;
    height: auto;
    padding: 10px;
  }

  .add button {
    margin: auto;
  }
}