.username {
  margin: 10px;
  padding: 10px 0;
  display: inline-block;
  max-width: 90%;
  width: 240px;
}

.username label {
  display: block;
  font-size: 1.2em;
  margin-bottom: 5px;
  font-weight: bold;
}

.username input {
  display: block;
  font-size: 1.2em;
  width: calc(100% - 10px);
}